"use client";
import { useEffect, useState } from "react";
import { getSolutionBySlug } from "@/services/solutionServices";
import SolutionDetails from "@/components/sections/SolutionDetails";

const SolutionDetailPage = ({ params }: { params: { title: string } }) => {
  const [solution, setSolution] = useState<any>(null);

  useEffect(() => {
    const fetchSolution = async () => {
      console.log(params.title);
      
      const response = await getSolutionBySlug(params.title);
      setSolution(response.data);
    };

    fetchSolution();
  }, [params.title]);

  if (!solution) {
    return <div>Loading...</div>;
  }

  return <SolutionDetails solution={solution} />;
};

export default SolutionDetailPage;